<template>
  <div>
    <template v-if="!userId">
      <router-link :to="{
        name : 'Login'
      }" class="router-link">登录</router-link>
    </template>
    <template v-else>
      <User />
    </template>
  </div>
</template>

<script>

import { mapState } from 'vuex';



export default {
  data() {
    return {
      isLogon: true, // 是否已经注册过了
      dialogTableVisible: false, // 控制登录面板的出现与消失
    };
  },
  components: {
    User : () => import("../User")
  },
  computed : {
    ...mapState("User", ["username","userId"]),
  },
  methods: {
    handleChangeCollapse(isLogon) {
      this.dialogTableVisible = false;
      this.isLogon = isLogon;
      setTimeout(() => {
        this.dialogTableVisible = true;
      }, 500); // 优化切换效果
    },
    handleLogoned() {
      // 已经有注册账号
      this.handleChangeCollapse(true); // 切换到登录组件
    }
  },
};
</script>

<style scoped>
.router-link {
  display: inline-block;
  width: 66px;
  height: 78px;
  line-height: 78px;
  text-align: center;
  color : #8982ff
}
</style>

