<template>
  <div class="nav-wrapper">
    <div class="nav-content">
      <el-row class="hidden-xs-only">
        <el-col :span="5">
          <h1 class="logo">
            <a href="/"
              >
              <img src="@/assets/ylogo.png" alt="" /> 
              <span>创享</span></a
            >
          </h1>
        </el-col>
        <el-col :span="8" class="search-box">
          <el-input
            placeholder="请输入你要找的歌"
            suffix-icon="el-icon-search"
            v-model="searchKeyWord"
            class="search-input"
            @input="handleSearch"
            @focus="showSearchResult = true"
            @blur="handleShowSearchResults"
          >
          </el-input>
          <div
            class="search-result"
            v-show="showSearchResult && keywords.length > 0"
          >
            <router-link
              class="search-item"
              :to="{
                name: 'Search',
                query: {
                  keyword: item.title,
                  prop: item.prop,
                },
              }"
              v-for="(item, i) in keywords"
              :key="i"
            >
              {{ item.title }}
            </router-link>
          </div>
        </el-col>
        <el-col :md="6" :sm="6" class="about-wrapper">
          <About />
        </el-col>
        <el-col :md="4" :sm="4">
          <LoginCollapse />
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import About from "./About";
import LoginCollapse from "@/components/LoginCollapse";
import { debounce } from "@/utils/search";
export default {
  data() {
    return {
      searchKeyWord: "",
      keywords: [],
      showSearchResult: false,
    };
  },
  components: {
    About,
    LoginCollapse, // 登录组件
  },
  methods: {
    handleSearch: debounce(async function () {
      // 做防抖处理
      const res = await this.$api.searchKeyWord({
        keyword: this.searchKeyWord,
      });
      if (res.code !== 200) {
        this.$message({
          message: "有点小问题",
          type: "warning",
          duration: 1500,
        });
        return;
      }
      this.keywords = // 仅展示5条结果
        res.data.length >= 5 ? res.data.slice(0, 5) : res.data.slice(0);
    }, 500),

    handleShowSearchResults() {
      setTimeout(() => {
        this.$data.showSearchResult = false;
      }, 500);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/myStyle.less";
/deep/ .el-input__inner {
  border-radius: 0px !important;
}
.nav-wrapper {
  width: 100%;
  background-color: #fff;
  height: 78px;
  border-bottom: 1px solid #eee;
  .nav-content {
    width: 1140px;
    .mx-auto();
    .logo {
      height: 78px;
      line-height: 78px;
      img {
        margin-top: 9px;
        width: 60px;
        height: 60px;
      }
      span {
        color: #fff;
        text-shadow: -1px -1px 3px rgba(0, 0, 0, 0.3),
          1px 1px 2px rgba(0, 0, 0, 0.3);
        font-size: 40px;
        height: 78px;
        vertical-align: top;
        margin-left: 10px;
      }
    }
    .search-box {
      margin-right: 20px;
      position: relative;
    }
    .search-result {
      width: 100%;
      box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
      box-sizing: border-box;
      border: 1px solid #ddd;
      position: absolute;
      background-color: #fff;
      z-index: 500;
      padding: 5px 10px;
      max-height: 300px;
      overflow-y: auto;
      font-size: 0.85rem;
      .search-item {
        cursor: pointer;
        padding: 5px;
        display: block;
        &:hover {
          background-color: #eee;
        }
      }
    }
    .search-input,
    button {
      margin-top: 18px;
    }
  }
}
</style>